import React, { useEffect, useRef, useState } from 'react';
import { Alert, Button, Card, Form } from 'react-bootstrap';
import { Link, useNavigate } from 'react-router-dom';

import { useAuth } from '../context/AuthContext';

export default function UpdateProfile() {
  // Form fields
  const emailRef = useRef()
  const passwordRef = useRef()
  const passwordConfirmRef = useRef()

  // States
  const { currentUser, updateUserEmail, updateUserPassword } = useAuth()
  const [error, setError] = useState('')
  const [loading, setLoading] = useState(false)
  const navigate = useNavigate();

  // Cancel all subscriptions and asynchronous tasks in a useEffect cleanup function
  // https://developer.mozilla.org/en-US/docs/Web/API/AbortController/abort  
  useEffect(() => {
    const controller = new AbortController();
    return () => {
      controller.abort();
    }
  }, [])

  function handleSubmit(e) {
    e.preventDefault();
    
    if (passwordRef.current.value !== passwordConfirmRef.current.value) {
      return setError('Password do not match')
    }

    const promise = []
    setError('')
    setLoading(true)

    if (emailRef.current.value !== currentUser.email) {
      promise.push(updateUserEmail(emailRef.current.value))
    }
    if (passwordRef.current.value) {
      promise.push(updateUserPassword(passwordRef.current.value))
    }

    Promise.all(promise).then(() => {
      navigate('/')
    }).catch((err) => {
      setError('Failed to update account')
      console.log(err);
    }).finally(() => {
      setLoading(false)
    })
  }

  return (
    <>
      <Card>
        <Card.Body>
          <h2 className="text-center mb-4">Update Profile</h2>
          {error && <Alert variant="danger">{error}</Alert>}          
          <Form onSubmit={handleSubmit}>
            <Form.Group id="email">
              <Form.Label>Email</Form.Label>
              <Form.Control type="email" ref={emailRef} required defaultValue={currentUser.email}></Form.Control>
            </Form.Group>
            <Form.Group id="password">
              <Form.Label>Password</Form.Label>
              <Form.Control type="password" ref={passwordRef} placeholder="Leave blank to keep the same"></Form.Control>
            </Form.Group>
            <Form.Group id="password-confirm">
              <Form.Label>Password Confirmation</Form.Label>
              <Form.Control type="password" ref={passwordConfirmRef} placeholder="Leave blank to keep the same"></Form.Control>
            </Form.Group>
            <Button disabled={loading} className="w-100 mt-4" type="submit">Update</Button>
          </Form>
        </Card.Body>
      </Card>
      <div className="w-100 text-center mt-2">
         <Link to="/">Cancel</Link>
      </div>
    </>
  )
}
